<template>
  <div>
    <div class="book-list">
      <div class="demo-book-box" v-for="(item, index) in dataList" :key="index">
        <div class="left-book">
          <img
            :src="item.cover || require('@/assets/images/default_img.jpg')"
            alt=""
            @click="toDetail(item)"
          />
        </div>
        <div class="right-book-info">
          <div class="book-title">
            <div class="book-label" v-if="item.sourceFile">
              {{ item.sourceFile }}
            </div>
            <div
              class="el-tooltip title"
              tabindex="0"
              @click="toDetail(item)"
              v-html="
                eachColor(
                  item.goodsTitle,
                  searchName,
                  $store.state.read.searchText
                )
              "
            ></div>
          </div>
          <div
            class="author-p"
            v-if="
              item.goodsType != 'paper' &&
              item.goodsType != 'looseleafOrtextbook' &&
              item.goodsType != 'train'
            "
          >
            作者：<span v-if="item.author"> {{ item.author }}</span>
            <span v-if="!item.author">-</span>
          </div>
          <div v-if="item.goodsType == 'train'" class="author-p">
            讲师：{{ item.lecturer || "-" }}
          </div>
          <div
            class="author-p"
            v-if="item.instName && item.goodsType == 'train'"
          >
            提供机构：{{ item.instName || "-" }}
          </div>
          <div
            class="author-p"
            v-if="item.publishingHouse && item.goodsType != 'train'"
          >
            提供机构：{{ item.publishingHouse || "-" }}
          </div>
          <div class="label" v-if="item.keyword != null">
            <span
              v-for="(item, index) in item.keyword.split(',')"
              :key="index"
              >{{ item }}</span
            >
          </div>
          <div class="price">
            {{ item.realPrice | tofixed2("￥") }}
            <span
              v-if="item.prices > 0"
              :class="item.prices == 0 ? 'yprice2' : 'yprice'"
              >{{ item.prices | tofixed2("￥") }}</span
            >
          </div>
          <!-- <div class="editor-l">编辑推荐语：</div> -->
          <p>
            {{
              item.authorRecommendation == null ||
              item.authorRecommendation == ""
                ? "-"
                : item.authorRecommendation
            }}
          </p>
        </div>
      </div>
    </div>
    <noData v-if="total == 0"></noData>
    <div class="pagination-box" v-show="total > 0">
      <el-pagination
        class="ou-pagination"
        background
        hide-on-single-page
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryParams.pageNum"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="queryParams.pageSize"
        layout="total, prev, pager, next, sizes, jumper, slot"
        :total="total"
      >
        <el-button @click="getList()" class="ou-page-go">GO</el-button>
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { selectBooks } from "@/api/index/index";
import noData from "@/components/noData.vue";
export default {
  data() {
    return {
      dataList: [],
      queryParams: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0,
    };
  },
  props: {
    searchName: String,
    eachColor: Function,
  },
  components: {
    noData,
  },
  methods: {
    getList(word = undefined) {
      //获取教学属性列表
      var Params = {
        ...this.queryParams,
        word2: word || undefined,
        word1: this.$store.state.read.searchText || undefined,
      };
      selectBooks(Params).then((res) => {
        this.dataList = res.rows;
        this.total = res.total;
        this.$emit("settotal", "tstotal", res.total);
      });
    },
    //每页条数
    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getList();
    },
    //当前页面
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getList();
    },
    toDetail(item) {
      if (item.goodsType == "train") {
        this.$router.push({
          path: "/willingLearn/bookstoreDetail",
          query: {
            goodsId: item.goodsId,
            goodsType: item.goodsType,
          },
        });
      } else {
        this.$router.push({
          path: "/bookstore/bookstoreDetail",
          query: {
            goodsId: item.goodsId,
            goodsType: item.goodsType,
          },
        });
      }
    },
  },
};
</script>

